body {
    font-family: "Caveat";
}

.img-thumbnail {
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.img-thumbnail:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    /* box-shadow: 1px; */
}
